import React from 'react';
import PropTypes from 'prop-types';

import * as classes from './PageBlock.module.css';

const PageBlock = ({ children, className, style, order, dynamicHeight }) => {
    const wrapperClass = [
        (order + 1) % 2 ? classes.pageBlockOdd : classes.pageBlockEven,
        classes.pageBlockWrapper,
        dynamicHeight ? '' : classes.pageBlockWrapperFixed,
    ].join(' ');

    const blockClass = [className, classes.pageBlock].join(' ');

    return (
        <div className={wrapperClass}>
            <div className={blockClass} style={{ height: '100%' }}>
                {children}
            </div>
        </div>
    );
};

PageBlock.propTypes = {
    children: PropTypes.node,
    className: PropTypes.string,
    style: PropTypes.string,
    order: PropTypes.number,
    dynamicHeight: PropTypes.bool,
};

PageBlock.defaultProps = {
    order: 0,
    dynamicHeight: false,
};

export default PageBlock;
